<template>
	<view class="service-lists">
		<navigator v-for="(item,index) in lists" :key="index" class="item" hover-class="none" :url="'./show?id='+item.id">
			<image :src="item.thumb" class="thumb"></image>
			<view class="info">
				<view class="title">
					<view>{{item.name}}</view>
					<view class="small">{{item.description}}</view>
				</view>
				<view class="btn">{{item.price}}起</view>
			</view>
		</navigator>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				cateid: 0,
				lists: []
			}
		},
		onLoad(option) {
			this.cateid = option.id
			this.getServices()
		},
		methods: {
			async getServices() {
				let res = await this.$u.get('/v2/service/index', {
					cateid: this.cateid
				})
				this.lists = res.data
			}
		}
	}
</script>
<style>
	page {
		background-color: #F5F5F5;
	}
</style>
<style scoped>
	.service-lists .item {
		margin: 30rpx;
		display: flex;
		flex-direction: column;
		background-color: #fff;
		border-radius: 16rpx;
		padding: 20rpx;
	}

	.service-lists .item .thumb {
		width: 652rpx;
		height: 296rpx;
		border-radius: 10rpx;
	}

	.service-lists .item .info {
		height: 160rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex: 1;
	}

	.service-lists .item .info .title {
		font-size: 32rpx;
		font-weight: bold;
		margin-top: 20rpx;
	}

	.service-lists .item .info .small {
		font-size: 24rpx;
		color: #999999;
		font-weight: normal;
		margin-top: 10rpx;
	}

	.service-lists .item .info .btn {
		margin-top: auto;
		margin-left: auto;
		font-size: 24rpx;
		border-radius: 34rpx;
		padding:0 20rpx;
		height: 60rpx;
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		line-height: 60rpx;
		color: #fff;
	}
</style>
